body {
  background-color: beige;
}

.loader {
  width: 80px;
  height: 80px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-radius: 50%;
  justify-content: center;
  font-weight: bold;
  animation: rotation 1s linear infinite;
  display: flex;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 88px;
  height: 88px;
  border: 3px solid #ff3d00;
  border-color: #ff3d00 rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.loader-container {
  height: 75vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.cat-info {
  text-align: center;
  margin-top: 50px;
  display: none;
}

.breed-select {
  width: 200px;
}

.cat-image {
  max-width: 100%;
  max-height: 300px;
}

